import React from "react";
import classes from "./BuildControls.module.css";
import BuildControl from "./BuildControl/BuildControl";



const buildControls = props => (
    <div className={classes.BuildControls}>
        <p>Current Price: <strong>{props.totalPrice.toFixed(2)}$</strong></p>
        {props.controls.map(ctrl => <BuildControl key={ctrl.label} 
        label={ctrl.label} 
        type={ctrl.type} 
        ingredientAdd={props.ingredientAdd}
        ingredientRemove={props.ingredientRemove}
        isRemoveIngredientDisalbled={props.isRemoveIngredientDisalbled}
        />)}
        <button className={classes.OrderButton} disabled={!props.isBurgerPurchasable} onClick={props.orderClick}>ORDER NOW</button>
    </div>
);


export default buildControls;