import React from "react";
import Aux from "../../../hoc/Auxx";
import Modal from "../../UI/Modal/Modal";
import Button from "../../UI/Button/Button";

const orderSummary = props => (
    <Aux>
        <h3>Your order </h3>
        <p>A delicious burger with the following ingredients:</p>
        <ul>
            {Object.keys(props.ingredients).map(ing => <li key={ing}><span style={{ textTransform: "capitalize" }}>{ing}</span>: {props.ingredients[ing]}</li>)}
        </ul>
        <p>Continue to Checkout?</p>
        <Button label="Cancel" btnType="Danger" clicked={props.backdropClicked}/>
        <Button label="Continue" btnType="Success" />

    </Aux>
);


    


export default Modal(orderSummary);