import React, {Component} from "react";
import Aux from "../../hoc/Auxx";
import Burger from "../../components/Burger/Burger";
import BuildControls from "../../components/Burger/BuildControls/BuildControls";
import OrderSummary from "../../components/Burger/OrderSummary/OrderSummary";


const ingredientPrice = {
    salad: 0.5,
    bacon: 0.7,
    cheese: 0.4,
    meat: 1.3,
}

class BurgerBuilder extends Component {
    
    state = {
        ingredients: {
            salad: 0,
            bacon: 0,
            cheese: 0,
            meat: 0
        },
        totalPrice: 2,
        ordered: false
    };

    firstToUpper = ([first, ...rest]) => first.toUpperCase() + rest.reduce((acc, x) => acc + x);

    addIngredientHandler = type => {
        return this.setState(prev => ({
            ingredients: {
                ...prev.ingredients,
                [type]: prev.ingredients[type] + 1
            },
            totalPrice: prev.totalPrice + ingredientPrice[type],
        }));
    };

    isRemoveIngredientDisalbled = type => this.state.ingredients[type] === 0;

    isBurgerPurchasable = () => Object.keys(this.state.ingredients).map(ing => this.state.ingredients[ing]).reduce((sum, x) => sum + x, 0) > 0;

    removeIngredientHandler = type => {
        return this.state.ingredients[type] > 0 ? this.setState(prev => ({
            ingredients: {
                ...prev.ingredients,
                [type]: prev.ingredients[type] - 1
            },
            totalPrice: prev.totalPrice - ingredientPrice[type]
        })) : undefined
    };

    orderedHandler = () => {
        this.setState((prev) => ({
            ordered: true
        }));
    }

    unorderedHandler = () => {
        this.setState(prev => ({
            ordered: false
        }))
    }



    render() {
        return (
            <Aux>
                <OrderSummary show={this.state.ordered} ingredients={this.state.ingredients} backdropClicked={this.unorderedHandler}/>
                <Burger ingredients={this.state.ingredients}/>
                <BuildControls 
                    controls={Object.keys(this.state.ingredients).map(ing => ({
                        label: this.firstToUpper(ing),
                        type: ing
                    }))} 
                    ingredientAdd={this.addIngredientHandler} 
                    ingredientRemove={this.removeIngredientHandler}
                    isRemoveIngredientDisalbled={this.isRemoveIngredientDisalbled}
                    totalPrice={this.state.totalPrice}
                    isBurgerPurchasable={this.isBurgerPurchasable()}
                    orderClick={this.orderedHandler}
                />
            </Aux>
        );
    }
}

export default BurgerBuilder;