import React from "react";
import classes from "./BuildControl.module.css";


const buildControl = props => (
    <div className={classes.BuildControl}>
        <div className={classes.Label}>{props.label}</div>
        <button className={classes.Less} onClick={() => props.ingredientRemove(props.type)} disabled={props.isRemoveIngredientDisalbled(props.type)}>Less</button>
        <button className={classes.More} onClick={() => props.ingredientAdd(props.type)}>More</button>
    </div>
);

export default buildControl;