import React from "react";
import classes from "./Burger.module.css";
import BurgerIngredient from "./BurgerIngredient/BurgerIngredient";


const burger = props => {

    const toBurgerIngredients = Object.keys(props.ingredients)
        .map(ing => [...Array(props.ingredients[ing])].map((_, index) => <BurgerIngredient key={ing + index} type={ing} />))
        .reduce((acc, x) => [...acc, ...x], []);

    return (
        <div className={classes.Burger}>
            <BurgerIngredient type="bread-top" />
            {
                toBurgerIngredients.length === 0 ?
                    <p>Insert your ingredients</p> :
                    toBurgerIngredients
            }
            <BurgerIngredient type="bread-bottom" />
        </div>
    );


}

export default burger;